.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 16px;
  padding-bottom: 56px;
  border-bottom: 1px solid #C4C4C4;

  .containerTitle {
    font-weight: bold;
    font-size: 14px;
    line-height: 24px;
    text-transform: uppercase;
    color: #91A0B6;
  }

  @media screen and (max-width: 570px) {
    margin-top: 8px;
    padding-bottom: 0;
  }
}

.articles {
  display: flex;
  justify-content: space-between;
  padding-top: 16px;

  @media screen and (max-width: 570px) {
   flex-direction: column;
  }
}
